.user {
    display: grid;
    grid-template-columns: 2fr 10fr;
    padding: 10px;
    border: solid gray 2px;
    margin-bottom: 10px;
}
.description {
    display: grid;
    grid-template-columns: 5fr 5fr;
}
.userPhoto {
    height: 100px;
    width: 100px;
}
.loader {
    float: right;
    padding-right: 600px;
}

